
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active for <2.1.8 */
{
  opacity: 0;
  transform: translateX(30px);
}
.removed-row {
  background-color: rgba(44, 62, 80, 0.2);
}
.removed {
  font-style: italic;
  font-size: .8em;
}



