/* Spacing
–––––––––––––––––––––––––––––––––––––––––––––––––– */
@import "../node_modules/vue-multiselect/dist/vue-multiselect.min.css";
input,
textarea,
select,
fieldset {
  margin-bottom: 0;
}
html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Raleway', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background-color: #EEEEEE;
}
input[type="text"] {
  border-radius: 3px;
  height: 33px;
}
a {
  text-decoration: none;
  color: #FD971F;
  cursor: pointer;
  transition: color .2s;
}
a:hover {
  color: #41B883;
}
.error-color {
  color: #FF4949;
}
#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  height: 100%;
  overflow-x: auto;
  overflow-y: hidden;
}
#app .page-header {
  -ms-flex: none;
      flex: none;
}
#app .page-content {
  -ms-flex: 1;
      flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  max-width: 1440px;
  border-right: 1px dashed #cecece;
}
#app .page-content .content-container {
  background-color: #242424;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  width: 100%;
  min-width: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
#app .page-content .content-container .main-content {
  background: #eee;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  overflow-y: auto;
  overflow-x: hidden;
  min-width: 300px;
  padding: 20px;
}
#app ul.list-unstyled {
  list-style: none;
}
#app table {
  border-collapse: collapse;
}
#app tbody.clickableRows tr {
  cursor: pointer;
  transition: background-color 0.2s;
}
#app tbody.clickableRows tr:hover {
  background-color: rgba(131, 145, 165, 0.08);
}
#app .without-bottom-margin {
  margin-bottom: 0;
}
#app .button-primary {
  background-color: #FD971F;
  border-color: #c17517;
  transition: all .2s;
}
#app .button-primary:hover {
  background-color: #c17517;
}
.toasted .fa {
  margin-left: -5px;
  margin-right: 5px;
}



