
@import "../styles/variables.less";
input {
  transition: all .3s;
}
.inner-addon .fa.clear-field {
  cursor: pointer;
  right: 0;
  left: auto;
  opacity: .4;
  transition: all .2s;
}
.inner-addon .fa.clear-field:hover {
  opacity: 1;
}
.inner-addon {
  position: relative;
}

/* style icon */
.inner-addon .fa {
  position: absolute;
  padding: 9px;
  pointer-events: auto;
}

/* align icon */
.left-addon .fa {
  left: 0px;
}
.right-addon .fa {
  right: 0px;
}

/* add padding  */
.left-addon input {
  padding-left: 30px;
}
.right-addon input {
  padding-right: 30px;
}
input.is-special {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  outline: none;
  border: 1px solid transparent;
  border-bottom: 1px solid #bfbfbf;
  background: transparent;
  margin-bottom: 10px;
  border-radius: 0;
}
input.is-special:focus {
  border: 1px solid transparent;
  border-bottom: 1px solid #8391a5;
}




