
.arij-tag {
  background-color: #41B883;
  display: inline-block;
  padding: 0 5px;
  height: 22px;
  line-height: 20px;
  font-size: 12px;
  color: #fff;
  border-radius: 4px;
  box-sizing: border-box;
  border: 1px solid transparent;
  white-space: nowrap;
  margin-right: 3px;
}
.special {
  background-color: #41B883;
  border-color: #51d896;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.5);
}



