
.error-message {
  font-size: .7em;
  color: #FF4949;
}
.slide-fade-enter-active {
  /*transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);*/
  transition: all 0.5s cubic-bezier(0.63, 0.11, 0.83, 1.6);
}
.slide-fade-leave-active {
  transition: all .25s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateY(-5px);
  opacity: 0;
}



